import React, { Component } from "react";
import appConfig from "../config/app";
import RequestUtil from "../utils/request";
import DateUtil from "../utils/date";
import SearchBar from "../components/SearchBar";

const _apiToken =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VySWQiOiJuaXJhbmQiLCJpYXQiOjE1ODgyNTg3MzB9.XGQmtTbHtcYdLd0FWnOlUJEsupww9lesbDZ1qULvDY0" ||
  null;

export default class News extends Component {
  constructor(props) {
    super(props);
    this.state = {
      keyword: "",
      newsTypeList: [],
      currentTabIndex: 0,
      currentPage: 0,
      news: [],
      showMore: false,
      resultText: "",
    };
  }

  componentDidMount() {
    this.getData();
  }

  renderRedirect = (id) => {
    this.props.history.push(`/newsdetails/${id}/${_apiToken}`);
  };

  render() {
    return (
      <div style={styles.wrapperDiv}>
        <SearchBar
          keyword={this.state.keyword}
          onChangeText={this.handleChangeText}
          onSearch={this.handleSearch}
          placeholder="หัวข้อ หรือ เนื้อหาข่าว"
        />

        <div
          style={{
            height: 8,
          }}
        />
        {this.state.news.length === 0 && (
          <h3
            style={{
              marginTop: 8,
              color: "#262626",
              fontWeight: "bold",
              textAlign: "center",
              fontSize: 14,
            }}
          >
            {this.state.resultText}
          </h3>
        )}

        {this.state.news.map((item) => (
          <div
            key={item.id}
            style={{
              marginBottom: 8,
              backgroundColor: "#ffffff",
              borderRadius: 2,
            }}
            onClick={() => {
              if (!item.isRead) {
                RequestUtil.fetchJSON(appConfig.api.news.updateNewsUserAccess, {
                  id: item.id,
                }).then((response) => {});
              }
              if (item.url) {
                window.open(item.url, "_blank");
              } else {
                this.renderRedirect(item.id);
              }
            }}
          >
            {(item.image || !item.isRead) && (
              <div
                style={{
                  width: "100%",
                  height: "auto",
                }}
              >
                {item.image && <img src={item.image} alt="Pic" width="100%" />}
                {!item.isRead && (
                  <div
                    style={{
                      height: 32,
                      backgroundColor: "#eb1c24",
                      opacity: 0.7,
                      bottom: 0,
                      width: "100%",
                      textAlign: "center",
                    }}
                  >
                    <label
                      style={{
                        color: "#ffffff",
                        fontWeight: "bold",
                        fontSize: 14,
                      }}
                    >
                      NEW
                    </label>
                  </div>
                )}
              </div>
            )}
            <div
              style={{
                paddingLeft: 16,
                paddingTop: 8,
                paddingBottom: 8,
              }}
            >
              <label
                style={{
                  fontSize: 18,
                  fontWeight: "bold",
                }}
              >
                {item.title}
              </label>
            </div>
            <div
              style={{
                paddingLeft: 16,
                paddingBottom: 8,
              }}
            >
              <label
                style={{
                  fontSize: 12,
                  color: "#eb1c24",
                }}
              >
                {`Date: ${DateUtil.getFormattedDate(item.date)}`}
              </label>
              <label
                style={{
                  fontSize: 12,
                }}
              >
                {` | By: ${item.postedBy}`}
              </label>
            </div>
          </div>
        ))}

        {this.state.showMore && (
          <button
            style={{
              width: "100%",
              paddingVertical: 8,
              alignItems: "center",
              marginBottom: 8,
              backgroundColor: "#eb1c24",
              borderColor: "#70130a",
              borderWidth: 1,
            }}
            onClick={() => {
              this.handleSearch(this.state.keyword, true);
            }}
          >
            <label
              style={{
                paddingLeft: 2,
                paddingBottom: 4,
                fontWeight: "bold",
                fontSize: 14,
                color: "#fff",
              }}
            >
              แสดงข่าวเพิ่ม
            </label>
          </button>
        )}
      </div>
    );
  }

  handleChangeText = (keyword) => {
      console.log(keyword)
    this.setState({
      keyword,
    });
  };

  handleSearch = (keyword, fetchNextPage = false) => {
    this.setState({
      resultText: "",
    });

    RequestUtil.fetchJSON(appConfig.api.news.search, _apiToken, {
      q: keyword,
      d: this.state.newsTypeList[this.state.currentTabIndex].id,
      p: fetchNextPage ? this.state.currentPage + 1 : 0,
    })
      .then((response) => {
        console.log(response);
        if (response.data) {
          if (fetchNextPage) {
            this.setState({
              news: this.state.news.concat(response.data),
              currentPage:
                response.data.length > 0
                  ? this.state.currentPage + 1
                  : this.state.currentPage,
              showMore: response.data.length >= 10,
            });
          } else {
            this.setState(
              {
                news: response.data,
                currentPage: 0,
                showMore: response.data.length >= 10,
                resultText: response.data.length > 0 ? "" : "ไม่พบข้อมูล",
              }
              //() => this.scrollViewRef.scrollTo({ x: 0, y: 0, animated: false })
            );
          }
        } else {
          console.log("error");
        }
      })
      .catch((error) => {
        this.setState({
          news: [],
          currentPage: 0,
          showMore: false,
          resultText: "เกิดข้อผิดพลาด กรุณาลองอีกครั้ง",
        });
      });
  };

  getData() {
    RequestUtil.fetchJSON(appConfig.api.news.getDepartments, _apiToken, {})
      .then((response) => {
        console.log("==>load");
        if (response.data) {
          this.setState(
            {
              newsTypeList: response.data,
            },
            () => {
              this.handleSearch(this.state.keyword);
            }
          );
        } else {
          console.log("error!!!");
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
}

var styles = {
  wrapperDiv: {
    backgroundColor: "#dfdfdf",
    paddingLeft: 8,
    paddingRight: 8,
  },
};
