import "rc-tabs/assets/index.css";
import React, { Component } from "react";
import Tabs, { TabPane } from "rc-tabs";
import TabContent from "rc-tabs/lib/SwipeableTabContent";
import SwipeableInkTabBar from "rc-tabs/lib/SwipeableInkTabBar";

const contentStyle = {
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  height: "100px",
  backgroundColor: "#fff",
};

const tabTitle = (key) => (
  <div style={{}} data-extra="tab-bar-title">{`Tab ${key}`}</div>
);
const makeTabPane = (key) => (
  <TabPane
    tab={tabTitle(key)}
    data-extra="tabpane"
    key={`${key}`}
  >
    <div style={contentStyle}>{`Tab ${key} `}</div>
  </TabPane>
);

const makeMultiTabPane = (count) => {
  const result = [];
  for (let i = 0; i < count; i++) {
    result.push(makeTabPane(i));
  }
  return result;
};

export default class About extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div>
        <h4>custom inkBar style</h4>
        <div>
          <Tabs
            renderTabBar={() => (
              <SwipeableInkTabBar
                pageSize={7}
                speed={5}
                styles={{
                  inkBar: {
                    width: "40px",
                    backgroundColor: "red",
                  },
                }}
              />
            )}
            renderTabContent={() => <TabContent />}
            defaultActiveKey="8"
          >
            {makeMultiTabPane(11)}
          </Tabs>
        </div>
      </div>
    );
  }
}
